<template>
  <div class="savings-goals">
    <h2>Future Savings Goals</h2>
    <form @submit.prevent="addGoal">
      <div class="form-group">
        <label>Goal Name</label>
        <input v-model="goalName" type="text" required placeholder="e.g. New Phone" />
      </div>
      <div class="form-group">
        <label>Target Amount</label>
        <input v-model.number="goalAmount" type="number" required placeholder="e.g. 20000" />
      </div>
      <button type="submit">Add Goal</button>
    </form>
    <ul v-if="goals.length">
      <li v-for="goal in goals" :key="goal.id">
        <span>{{ goal.name }} - ₹{{ goal.amount }}</span>
        <button @click="removeGoal(goal.id)">Remove</button>
      </li>
    </ul>
    <div v-else class="empty">No savings goals yet.</div>
  </div>
</template>

<script setup>
import { ref, watch } from 'vue'

const goalName = ref('')
const goalAmount = ref(0)
const goals = ref(JSON.parse(localStorage.getItem('savingsGoals') || '[]'))

function addGoal() {
  if (goalName.value && goalAmount.value > 0) {
    goals.value.push({
      id: Date.now(),
      name: goalName.value,
      amount: goalAmount.value
    })
    goalName.value = ''
    goalAmount.value = 0
  }
}

function removeGoal(id) {
  goals.value = goals.value.filter(g => g.id !== id)
}

watch(goals, (newGoals) => {
  localStorage.setItem('savingsGoals', JSON.stringify(newGoals))
}, { deep: true })
</script>

