<template>
  <div class="categories">
    <h2>Categories</h2>
    
    <div class="add-category">
      <h3>Add New Category</h3>
      <form @submit.prevent="handleSubmit">
        <div class="form-group">
          <label>Name</label>
          <input type="text" v-model="name" required>
        </div>
        <div class="form-group">
          <label>Type</label>
          <select v-model="type" required>
            <option value="income">Income</option>
            <option value="expense">Expense</option>
          </select>
        </div>
        <button type="submit">Add Category</button>
      </form>
    </div>
    
    <div class="category-list">
      <h3>Your Categories</h3>
      <div v-if="categories.length === 0" class="empty">
        No categories yet
      </div>
      <ul v-else>
        <li v-for="category in categories" :key="category.id" :class="category.type">
          {{ category.name }}
          <span class="type">{{ category.type }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useBudgetStore } from '../store/useBudgetStore'

export default {
  name: 'CategoriesPage',
  setup() {
    const budgetStore = useBudgetStore()
    const name = ref('')
    const type = ref('expense')
    
    const categories = computed(() => budgetStore.categories)
    
    const handleSubmit = () => {
      budgetStore.addCategory({
        name: name.value,
        type: type.value
      })
      
      // Reset form
      name.value = ''
      type.value = 'expense'
    }
    
    return {
      name,
      type,
      categories,
      handleSubmit
    }
  }
}
</script>

