<template>
  <div class="settings">
    <h2>Settings</h2>
    
    <div class="setting-card">
      <h3>Currency</h3>
      <select v-model="budgetStore.currency" @change="budgetStore.setCurrency($event.target.value)">
        <option value="INR">₹ (INR)</option>
        <option value="USD">$ (USD)</option>
        <option value="EUR">€ (EUR)</option>
        <!-- Add more as needed -->
      </select>
    </div>
    
    <div class="setting-card">
      <h3>Theme</h3>
      <div class="theme-options">
        <label>
          <input type="radio" value="light" v-model="budgetStore.theme" @change="onThemeChange" />
          Light
        </label>
        <label>
          <input type="radio" value="dark" v-model="budgetStore.theme" @change="onThemeChange" />
          Dark
        </label>
        <label>
          <input type="radio" value="system" v-model="budgetStore.theme" @change="onThemeChange" />
          System
        </label>
      </div>
    </div>
    
    <div class="setting-card">
      <h3>Data Management</h3>
      <button @click="exportData" class="secondary">Export Data</button>
      <button @click="importData" class="secondary">Import Data</button>
      <button @click="resetData" class="danger">Reset All Data</button>
    </div>
  </div>
</template>

<script setup>

import { useBudgetStore } from '../store/useBudgetStore'

const budgetStore = useBudgetStore()

function onThemeChange(event) {
  budgetStore.setTheme(event.target.value)
}

const exportData = () => {
  const data = {
    transactions: budgetStore.transactions,
    categories: budgetStore.categories
  }
  const blob = new Blob([JSON.stringify(data)], { type: 'application/json' })
  const url = URL.createObjectURL(blob)
  const a = document.createElement('a')
  a.href = url
  a.download = 'budget-tracker-data.json'
  a.click()
  URL.revokeObjectURL(url)
}

const importData = () => {
  const input = document.createElement('input')
  input.type = 'file'
  input.accept = '.json'
  
  input.onchange = e => {
    const file = e.target.files[0]
    const reader = new FileReader()
    
    reader.onload = event => {
      try {
        const data = JSON.parse(event.target.result)
        // In a real app, you would validate the data first
        budgetStore.transactions = data.transactions || []
        budgetStore.categories = data.categories || []
        alert('Data imported successfully!')
      } catch (error) {
        alert('Error importing data: ' + error.message)
      }
    }
    
    reader.readAsText(file)
  }
  
  input.click()
}

const resetData = () => {
  if (confirm('Are you sure you want to reset all data? This cannot be undone.')) {
    budgetStore.transactions = []
    budgetStore.categories = [
      { id: 1, name: 'Food', type: 'expense' },
      { id: 2, name: 'Transport', type: 'expense' },
      { id: 3, name: 'Shopping', type: 'expense' },
      { id: 4, name: 'Entertainment', type: 'expense' },
      { id: 5, name: 'Salary', type: 'income' },
      { id: 6, name: 'Freelance', type: 'income' }
    ]
  }
}
</script>

