<template>
  <div class="add-transaction">
    <h3>Add New Transaction</h3>
    <form @submit.prevent="handleSubmit">
      <div class="form-group">
        <label>Type</label>
        <select v-model="type" required>
          <option value="income">Income</option>
          <option value="expense">Expense</option>
        </select>
      </div>
      <div class="form-group">
        <label>Category</label>
        <select v-model="categoryId" required>
          <option 
            v-for="category in filteredCategories" 
            :key="category.id" 
            :value="category.id"
          >
            {{ category.name }}
          </option>
        </select>
      </div>
      <div class="form-group">
        <label>Amount</label>
        <input type="number" v-model="amount" required>
      </div>
      <div class="form-group">
        <label>Description</label>
        <input type="text" v-model="description">
      </div>
      <div class="form-group">
        <label>Date</label>
        <input type="date" v-model="date" required>
      </div>
      <button type="submit">Add Transaction</button>
    </form>
  </div>
</template>

<script setup>
import { ref, computed, defineEmits, defineProps } from 'vue'
import { useBudgetStore } from '../store/useBudgetStore'

const props = defineProps({
  spendingLimit: Number
})
const emit = defineEmits(['limitExceeded'])

const amount = ref('')
const type = ref('expense')
const categoryId = ref(null)
const description = ref('')
const date = ref(new Date().toISOString().split('T')[0])

const budgetStore = useBudgetStore()

const filteredCategories = computed(() => {
  return budgetStore.categories.filter(c => c.type === type.value)
})

function handleSubmit() {
  const amt = parseFloat(amount.value)
  if (type.value === 'expense') {
    const newTotalExpense = budgetStore.totalExpense + amt
    if (props.spendingLimit > 0 && newTotalExpense > props.spendingLimit) {
      emit('limitExceeded', newTotalExpense)
      return
    }
  }
  
  const transaction = {
    type: type.value,
    categoryId: parseInt(categoryId.value),
    amount: parseFloat(amount.value),
    description: description.value,
    date: date.value
  }
  
  budgetStore.addTransaction(transaction)
  
  // Reset form
  amount.value = ''
  categoryId.value = null
  description.value = ''
  date.value = new Date().toISOString().split('T')[0]
}
</script>

