<script setup>
import { computed } from 'vue'
import { useBudgetStore } from '../store/useBudgetStore'
const budgetStore = useBudgetStore()

const upcoming = computed(() => budgetStore.getUpcomingRecurringTransactions())
</script>

<template>
  <div class="calendar">
    <h3>Upcoming Recurring Transactions</h3>
    <ul>
      <li v-for="tx in upcoming" :key="tx.id">
        <strong>{{ tx.name }}</strong> ({{ tx.type }}) - ₹{{ tx.amount }} on <span style="color: #42b983">{{ tx.nextDate }}</span>
      </li>
      <li v-if="upcoming.length === 0">No upcoming recurring transactions.</li>
    </ul>
  </div>
</template>

<style scoped>
.calendar {
  background: #fff;
  padding: 16px;
  border-radius: 8px;
}
</style>