<template>
  <div class="transaction-list">
    <h3>Recent Transactions</h3>
    <div v-if="transactions.length === 0" class="empty">
      No transactions yet
    </div>
    <table v-else>
      <thead>
        <tr>
          <th>Title</th>
          <th>Amount</th>
          <th>Category</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="tx in transactions" :key="tx.id">
          <td>
            <EditableCell
              v-model="tx.title"
              @update:modelValue="val => updateTransactionField(tx, 'title', val)"
            />
          </td>
          <td>
            <EditableCell
              v-model="tx.amount"
              type="number"
              @update:modelValue="val => updateTransactionField(tx, 'amount', val)"
            />
          </td>
          <td>
            <EditableCell
              v-model="tx.category"
              @update:modelValue="val => updateTransactionField(tx, 'category', val)"
            />
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script setup>
import { computed, defineProps } from 'vue'
import { useBudgetStore } from '../store/useBudgetStore'
import EditableCell from './EditableCell.vue'

const budgetStore = useBudgetStore()

const props = defineProps({
  limit: {
    type: Number,
    default: 0
  }
})

const transactions = computed(() => {
  const sorted = [...budgetStore.transactions].sort((a, b) => 
    new Date(b.date) - new Date(a.date)
  )
  return props.limit > 0 ? sorted.slice(0, props.limit) : sorted
})

function updateTransactionField(tx, field, value) {
  tx[field] = value
  // Optionally, persist the change to your store or backend here
}
</script>

