<script setup>
import { ref } from 'vue'

const userInput = ref('')
const messages = ref([
  { sender: 'bot', text: 'Hi! I am your Financial Tips Assistant. Ask me anything about saving, budgeting, expenses, or investments!' }
])
const loading = ref(false)

// Your Gemini API key


const systemPrompt = `You are a smart Financial Tips Assistant. Your job is to give friendly, clear, and practical financial advice or money-saving tips to users.

If a user asks:
- About saving money → give them a practical savings tip.
- About budgeting → share budgeting techniques.
- About managing expenses → give ways to cut costs.
- About investments → give basic beginner-friendly advice (without recommending specific stocks or products).
- Any other money question → answer it helpfully in simple language.

Always keep your responses short, helpful, and easy to understand. End with an emoji when appropriate (💰, 📉, 📈, 💡).`

async function sendToGemini(userMessage) {
  try {
    const response = await fetch('http://localhost:3001/api/gemini', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ userMessage, systemPrompt })
    })
    const data = await response.json()
    return (
      data?.candidates?.[0]?.content?.parts?.[0]?.text ||
      "Sorry, I couldn't get a response right now."
    )
  } catch (e) {
    return "Sorry, there was an error connecting to the advice service."
  }
}

async function handleSend() {
  const input = userInput.value.trim()
  if (!input) return
  messages.value.push({ sender: 'user', text: input })
  userInput.value = ''
  loading.value = true
  messages.value.push({ sender: 'bot', text: 'Thinking...' })
  const reply = await sendToGemini(input)
  messages.value.pop() // Remove "Thinking..."
  messages.value.push({ sender: 'bot', text: reply })
  loading.value = false
}
</script>

<template>
  <div class="chatbot">
    <div class="chat-window">
      <div v-for="(msg, i) in messages" :key="i" :class="['msg', msg.sender]">
        <span>{{ msg.text }}</span>
      </div>
    </div>
    <form @submit.prevent="handleSend" class="chat-input">
      <input v-model="userInput" :disabled="loading" placeholder="Type your money question..." autocomplete="off" />
      <button type="submit" :disabled="loading || !userInput.trim()">Send</button>
    </form>
  </div>
</template>

