<script setup>
import { ref } from 'vue'
import { useBudgetStore } from '../store/useBudgetStore'
const budgetStore = useBudgetStore()

const name = ref('')
const amount = ref('')
const type = ref('expense')
const date = ref('')
const frequency = ref('monthly')

function handleSubmit() {
  if (!name.value || !amount.value || !date.value) return
  budgetStore.addRecurringTransaction({
    name: name.value,
    amount: parseFloat(amount.value),
    type: type.value,
    date: date.value,
    frequency: frequency.value
  })
  name.value = ''
  amount.value = ''
  date.value = ''
  frequency.value = 'monthly'
}
</script>

<template>
  <form @submit.prevent="handleSubmit" class="recurring-form">
    <h3>Add Recurring Transaction</h3>
    <input v-model="name" placeholder="Name (e.g. Rent)" required />
    <input v-model="amount" type="number" placeholder="Amount" required />
    <select v-model="type">
      <option value="expense">Expense</option>
      <option value="income">Income</option>
    </select>
    <input v-model="date" type="date" required />
    <select v-model="frequency">
      <option value="monthly">Monthly</option>
      <option value="weekly">Weekly</option>
      <option value="yearly">Yearly</option>
    </select>
    <button type="submit">Add Recurring</button>
  </form>
</template>

<style scoped>
.recurring-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  background: #fff;
  padding: 16px;
  border-radius: 8px;
  margin-bottom: 24px;
}
</style>